import React, { useState } from "react";

function CustomSelect({ placeholder, options, selectValue, value, isStatic }) {
  let [isVisible, setVisible] = useState(false);
  window.addEventListener("click", () => {
    setVisible(false);
  });
  return (
    <div className="pos-cont">
      <input
        className="form-inp wi-80p cu-po"
        type="text"
        name="fname"
        id="fname"
        value={value}
        placeholder={placeholder}
        onChange={e => {
          selectValue(e.target.value);
          setVisible(true);
        }}
        disabled={isStatic && isVisible}
        onFocus={e => setVisible(true)}
        onBlur={e => setVisible(false)}
      />
      <div className="pos-abs-top-left z-3">
        <ul>
          {isVisible
            ? !isStatic
              ? options
                  .filter(option => option.includes(value))
                  .map(correctOption => (
                    <li
                      key={correctOption}
                      onClick={e => {
                        selectValue(correctOption);
                        setVisible(!isVisible);
                      }}
                      className="option-cust bg-light cu-po wi-100p"
                    >
                      {correctOption}
                    </li>
                  ))
              : options.map(correctOption => (
                  <li
                    onClick={e => {
                      selectValue(correctOption);
                      setVisible(!isVisible);
                    }}
                    key={correctOption}
                    className="option-cust bg-light cu-po wi-100p"
                  >
                    {correctOption}
                  </li>
                ))
            : null}
        </ul>
      </div>
    </div>
  );
}

export default CustomSelect;
